.content-container {
  margin: 0 0 90px 0;
  display: flex;
  justify-content: space-between;
  font-family: "Inter";
  color: var(--main-tx-color);
}

.content-left-side__title {
  max-width: 362px;
  margin: 0 0 30px 0;
  padding: 0;
  font-weight: 400;
  font-size: 48px;
  line-height: 58px;
}

.content-text-marker {
  margin: 0;
  padding: 0;
  font-weight: 900;
  background-image: linear-gradient(90deg, #fcb045, #ff3f78);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.content-text-marker_description {
  text-transform: uppercase;
}

.content-left-side__description {
  max-width: 275px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

.content-right-side {
}

.content-right-side__title {
  max-width: 291px;
  margin: 0 0 40px 0;
  padding: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: right;
}

.list-container-item__title-line {
  background-image: linear-gradient(90deg, #fcb045, #ff3f78);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.content-right-side__list-container {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 30px;
  grid-template-columns: repeat(2, 1fr);
  text-align: right;
}

.list-container-item__title {
  margin: 0 0 6px 0;
  padding: 0;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
}

.list-container-item__description {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}

.content-button {
  all: unset;
  margin: 60px 0 0 0;
  padding: 21px 35px;
  background-color: #4077f3;
  cursor: pointer;
}

@media screen and (max-width: 799px) {
  .content-container {
    margin: 0;
    padding-bottom: 60px;
    flex-direction: column;
    margin-left: 14px;
  }
  .list-container-item__title {
    margin-right: 25px;
    font-size: 13px;
    line-height: 16px;
    font-family: "Montserrat";
    font-weight: 400;
  }
  .content-left-side__title {
    margin-top: 40px;
    font-size: 36px;
    line-height: 43px;
  }
  .content-left-side__description {
    margin-bottom: 45px;
  }

  .content-text-marker_description {
    background-image: linear-gradient(2deg, #fcb045, #fd1d1d);
  }
  .content-right-side__title {
    text-align: left;
    margin-bottom: 20px;
  }
  .content-right-side__list-container {
    align-items: center;
    justify-items: left;
    gap: 0;
  }
}
